import "@servicenow/now-avatar";
import "@servicenow/now-label-value";
import "@servicenow/now-highlighted-value";
import "@servicenow/now-text-link";
import { LINK_ITEM_CLICKED } from "./constants";
import { Fragment } from "@servicenow/ui-renderer-snabbdom";

export default ({ properties }, { dispatch }) => {
	const {
		avatarSrc,
		headingName,
		headingBadges,
		subHeadingTitles,
		subHeadingBadges,
		content,
		hideBorder,
		headingLevel
	} = properties;
	if (!headingName) {
		console.error("ERROR:sn-contact-card component: Invalid contact name.");
		return;
	}
	const HeadingTag = `h${headingLevel}`;
	let contentItems = [];
	if (content && content.length) {
		content.map(item => {
			let itemValue = { ...item };
			itemValue.type = item.clickable ? "text-link" : "string";
			if (item.clickable) {
				itemValue.label = item.value;
				itemValue.actualLabel = item.label;
				itemValue.href = "javascript:void(0)";
				itemValue.id = item.id;
			} else itemValue.value = item.value;
			contentItems.push({
				label: item.label,
				value: itemValue
			});
		});
	}

	return (
		<div
			className={
				"sn-contact-card--container " + (!hideBorder ? "with-border" : "")
			}
		>
			<div className="sn-contact-card--avatar">
				<now-avatar
					userName={headingName}
					imageSrc={avatarSrc || ""}
					size="md"
					ariaHidden="false"
				/>
			</div>
			<div className="sn-contact-card--content">
				<div className="sn-contact-card--header">
					<div className="contact-head--main">
						<div className="contact-title">
							<HeadingTag className="main-title">{headingName}</HeadingTag>
						</div>
						{headingBadges ? (
							headingBadges.map(badge => (
								<div className="contact-badge">
									<now-highlighted-value
										label={badge.label}
										icon=""
										color={badge.color}
									/>
								</div>
							))
						) : (
							<Fragment />
						)}
					</div>
					{subHeadingTitles && subHeadingTitles.length ? (
						<div className="contact-head--sub">
							<div className="contact-sub-title">
								{subHeadingTitles.map(item => (
									<div
										className={"sub-title" + (item.clickable ? " --link" : "")}
									>
										<span className="--title">
											{item.clickable ? (
												<now-text-link
													label={item.label}
													href="javascript:void(0)"
													variant="primary"
													on-click={() => {
														dispatch(LINK_ITEM_CLICKED, {
															item
														});
													}}
												/>
											) : (
												item.label
											)}
										</span>
									</div>
								))}
							</div>
							{subHeadingBadges ? (
								subHeadingBadges.map(badge => (
									<div className="contact-badge">
										<now-highlighted-value
											label={badge.label}
											icon=""
											color={badge.color}
										/>
									</div>
								))
							) : (
								<Fragment />
							)}
						</div>
					) : (
						<Fragment />
					)}
				</div>
				{contentItems.length ? (
					<div className="sn-contact-card--body">
						<div className="contact-detail">
							<now-label-value-stacked
								items={contentItems}
								itemMinWidth="115px"
								delimiter=","
								size="sm"
								align="horizontal-equal"
								wrapped
								truncated
							/>
						</div>
					</div>
				) : (
					<Fragment />
				)}
			</div>
		</div>
	);
};
