import { createCustomElement } from "@servicenow/ui-core";
import rtlBehavior from "@servicenow/behavior-rtl";
import view from "./view";
import styles from "./styles.scss";
import { LINK_ITEM_CLICKED } from "./constants";

const contactCardComponentName = "sn-contact-card";

/**
 * Contact card component that shows user information, such as a user avatar, name, titles, and badges, inside of a card. 
 * 
 * 
 * ```jsx
 * <sn-contact-card
 * 		avatar-src="/4a31e5d43740310042106710ce41f1a0.iix"
 * 		heading-name="Julie Lewis"
 * 		heading-badges={[
 * 			{label: "VIP", color: "positive"},
 * 			{label: "9:34 AM PDT", color: "low"}
 * 		]}
 * 		sub-heading-titles={[
 * 			{label: "Network Administrator"},
 * 			{label: "Boxeo", clickable: true, id: "account-link"}
 * 		]}
 * 		sub-heading-badges={[
 * 			{label: "Escalated", color: "critical"}
 * 		]}
 * 		content={[
 * 			{label: "Mobile Phone", value: "+1 858 867 5306", clickable: true, id: "mobile-link"},
 * 			{label: "Business Phone", value: "+1 858 786 3506", clickable: true, id: "phone-link"},
 * 			{label: "Email", value: "julie.lewis@boxeo.com", clickable: true, id: "email-link"},
 * 			{label: "City", value: "San Diego"},
 * 			{label: "State", value: "California"}
 * 		]}
 * 		hide-border={true}
 * />
 * ```
 * @summary Provides contextual contact information about a contact or consumer.
 * 
 **/

createCustomElement(contactCardComponentName, {
	view,
	behaviors: [{ behavior: rtlBehavior }],
	properties: {
		/**
		 * Location of the user image to display in the avatar.
		 * @type {?string}
		 * @uib.label User Image source
		 */
		avatarSrc: { schema: { type: "string" } },
		/**
		 * Display name of the contact.
		 * @type {!string}
		 * @uib.label Heading Name
		 * @uib.description The display name of the contact
		 */
		headingName: {
			schema: { type: "string" },
			reflect: true,
			required: true
		},
		/**
		 * HTML heading element number used to render the name in heading.
		 * @type {number}
		 * @uib.label Heading Level
		 * @uib.description HTML heading element number used to render the name in the heading
		 * @uib.defaultValue 3
		 */
		headingLevel: {
			schema: { type: "number", enum: [1, 2, 3, 4, 5, 6] },
			default: 3
		},
		/**
		 * List of badges to show beside the contact's name.
		 * @type {?Array.<{label: string, color: string}>}
		 * @uib.label Heading Badges
		 * @uib.description List of badges to show beside the contact's name
		 */
		headingBadges: {
			schema: {
				type: "array",
				items: {
					type: "object",
					properties: {
						label: { type: "string" },
						color: {
							type: "string",
							enum: [
								"critical",
								"high",
								"warning",
								"moderate",
								"positive",
								"info",
								"low",
								"magenta",
								"pink",
								"orange",
								"yellow",
								"brown",
								"green",
								"green-yellow",
								"blue",
								"gray",
								"teal",
								"purple"
							]
						}
					}
				}
			}
		},
		/**
		 * Titles to show for the contact. Supports multiple titles.
		 * @type {?Array.<{label: string, clickable: ?boolean, id: ?string}>}
		 * @uib.label Sub heading titles
		 * @uib.description Titles to show for the contact. Supports multiple titles.
		 */
		subHeadingTitles: {
			schema: {
				type: "array",
				items: {
					type: "object",
					properties: {
						label: { type: "string" },
						clickable: { type: "boolean" },
						id: { type: "string" }
					}
				}
			}
		},
		/**
		 * Badges to show beside the titles. Supports multiple badges.
		 * @type {?Array.<{label: string, color: string}>}
		 * @uib.label Sub heading badges
		 * @uib.description Badges to show beside the titles. Supports multiple badges.
		 */
		subHeadingBadges: {
			schema: {
				type: "array",
				items: {
					type: "object",
					properties: {
						label: { type: "string" },
						color: {
							type: "string",
							enum: [
								"critical",
								"high",
								"warning",
								"moderate",
								"positive",
								"info",
								"low",
								"magenta",
								"pink",
								"orange",
								"yellow",
								"brown",
								"green",
								"green-yellow",
								"blue",
								"gray",
								"teal",
								"purple"
							]
						}
					}
				}
			}
		},
		/**
		 * List of fields to show for the contact.
		 * This is an array of objects that define the label/values displayed in the content.
		 *
		 * - `label`: String. Text displayed as a label. Required.
		 * - `value`: String. Value to show. Required.
		 *
		 * clickable-text type:
		 * - `clickable`: Boolean. Renders value as clickable text.
		 * - `id`: String. A unique identifier passed back in the payload when this item is clicked.
		 *
		 * @type {?Array.<{label: string, value: string, clickable: ?boolean, id: ?string}>}
		 * @uib.label Content
		 * @uib.description List of fields to be shown for the contact.
		 */
		content: {
			schema: {
				type: "array",
				items: {
					type: "object",
					properties: {
						label: { type: "string" },
						value: { type: "string" },
						clickable: { type: "boolean" },
						id: { type: "string" }
					}
				}
			}
		},
		/**
		 * Hides the border around the card.
		 * @type {?boolean}
		 * @uib.label Hide border
		 * @uib.description Select to hide the border around the card
		 * @uib.defaultValue false
		 */
		hideBorder: {
			schema: { type: "boolean" },
			default: false
		},
		/**
		 * When provided, screen readers read this label before reading the content inside the card.
		 * @type {string}
		 * @uib.label Aria Label
		 * @uib.description Forces screen readers to read this label before reading the content inside the card.
		 */
		ariaLabel: {
			reflect: true,
			schema: { type: "string" }
		}
	},
	styles,
	actions: {
		/**
		 * Dispatched when a `text-link` item is clicked on the contact card.
		 * Payload contains the `item` object that was provided through the content/subHeading properties.
		 * @type {{item: object}}
		 */
		[LINK_ITEM_CLICKED]: {
			schema: {
				type: "object",
				properties: {
					item: { type: "object" }
				}
			}
		}
	},
	actionHandlers: {
		"NOW_TEXT_LINK#CLICKED": ({ action }) => {
			action.stopPropagation();
		},
		"NOW_LABEL_VALUE_STACKED#TEXT_LINK_CLICKED": ({ action, dispatch }) => {
			const {
				payload: { item }
			} = action;
			if (item) {
				item.label = item.actualLabel;
				delete item.actualLabel;
				delete item.href;
				delete item.type;
				dispatch(LINK_ITEM_CLICKED, {
					item
				});
			}
			action.stopPropagation();
		}
	},
	dispatches: {
		/**
		 * Dispatched when a `text-link` item is clicked on the contact card.
		 * Payload contains the `item` object that was provided through the content/subHeading properties.
		 * 
		 * @type {{item: object}}
		 */
		'SN_CONTACT_CARD#ITEM_CLICKED': {}
	}
});
